.overlay-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  // background-color: purple;
  z-index: 2;
  // overflow: auto;
  // background-color: rgba(255, 255, 255, 0.9);
  .overlay-wrapper__inner {
    // border: 2px solid green;
    // background-color: red;
    // background-color: white;
    // width: fit-content;
    margin-left: auto;
    margin-right: auto;
    // width: 1000px;
    overflow: hidden;

    // !! hier was ik, ik probeerde de negatieve marge af te trekken zodat
    // ik ruimte heb voor het mouseout cursor effect
    // width: 500px;
    width: calc(100vw - #{$margin-xs} * 2);
    @include view-s-up {
      width: calc(100vw - #{$margin-s} * 2);
    }
    @include view-m-up {
      width: calc(100vw - #{$margin-m} * 2);
    }

    > * {
      margin-left: -$margin-xs;
      margin-right: -$margin-xs;

      @include view-s-up {
        margin-left: -$margin-s;
        margin-right: -$margin-s;
      }
      @include view-m-up {
        margin-left: -$margin-m;
        margin-right: -$margin-m;
      }
    }
  }
}

.overlay--preface {
  .overlay__content {
    // !! hier was ik, ik probeerde de negatieve marge af te trekken zodat
    // ik ruimte heb voor het mouseout cursor effect
    // width: 500px;
    // width: 500px;
    width: calc(100vw - #{$margin-xs} * 2);
    @include view-s-up {
      width: calc(100vw - #{$margin-s} * 2);
    }
    @include view-m-up {
      width: calc(100vw - #{$margin-m} * 2);
    }
    > * {
      margin-left: -$margin-xs;
      margin-right: -$margin-xs;

      @include view-s-up {
        margin-left: -$margin-s;
        margin-right: -$margin-s;
      }
      @include view-m-up {
        margin-left: -$margin-m;
        margin-right: -$margin-m;
      }
    }
  }
}

.home {
  background-color: var(--main-background-color);
  // Quote
  blockquote {
    padding: 0;
    margin: 0;
    @include font-body-m();
    cite {
      @include font-body();
      display: inline-block;
      margin-top: rem-calc(20);
      font-style: normal;
    }
  }

  //
  button {
    cursor: pointer;
    width: rem-calc(160);
    padding: 18px;
    color: var(--color-red);
    position: absolute;
    right: 0;
    bottom: rem-calc(-105);
    z-index: 1;
    @include font-mono();
    font-size: rem-calc(18);

    @include view-s-up() {
      width: rem-calc(200);
      font-size: rem-calc(24);
    }
    @include view-m-up() {
      width: rem-calc(252);
      font-size: rem-calc(32);
      right: span(1 wide of 12);
    }
  }
}

.home-block--howto,
.home-block--howto-community {
  overflow: hidden;
}
.home-block {
  // width: 100%;
  // width: 100%;
  // border: 1px solid red;
  // height: 150vh;

  svg {
    display: block;
    margin: 0 auto;
  }
}

// home block animation
.home-block--howto {
  color: var(--color-purple);
  > * {
    margin: 0 auto;
    width: span(9 of 12);

    svg {
      path {
        fill: currentColor;
      }
    }
  }
}

.home-block--howto-community {
  color: var(--color-purple);

  svg {
    width: 100%;
    height: auto;
  }
}
html[data-darkmode="true"] {
  .home-block--howto-community,
  .home-block--howto {
    color: var(--main-text-color);
  }
}

.home-block--quote {
  position: relative;
  .home-block__inner {
    width: span(10 of 12);
    margin: 0 auto;
  }
}

.card-style {
  background-color: var(--main-background-color);
  border: 2px solid currentColor;
  border-top: none;
  border-left: none;
  &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0.3;
    transition: box-shadow 250ms ease;
    box-shadow: 14px 14px 0 0 currentColor, 1px 1px 0 1px currentColor;
  }

  // &:hover:after {
  //   box-shadow: 18px 18px 0 0 currentColor;
  // }
}
.icon-number {
  display: block;
  @include aspect-ratio(99 64);
  svg {
    position: absolute;
    width: 100%;
    height: 100%;

    polygon,
    path {
      fill: currentColor;
    }
  }
}

