.grow-image {
  margin: 0 auto;

  figure {
    // display: block;
    // width: 100%;
    margin: 0 auto;
    margin-left: -$spacing-s;
    margin-right: -$spacing-s;

    // background-color: grey;

    // @include aspect-ratio(4 3);
  }

  @include view-s-up() {
    .grow-image__inner {
      transform-origin: top;
      transform: scale(0.5);
    }

    figure {
      width: 100%;
      margin-left: auto;
      margin-right: auto;
      width: span(12 of 12);

      &.is-portrait {
        width: span(8 of 12);
      }
    }
  }
}
