article.page {
  header {
  }

  .page-header {
    text-align: center;
    // width: span(10 of 12);

    margin-bottom: rem-calc(60);
    figure {
      margin-bottom: rem-calc(60);
    }
    h1 {
      @include font-heading-xl();
      margin-bottom: rem-calc(30);
    }
    span {
      @include font-mono();
      font-size: rem-calc(18);
      text-transform: uppercase;
    }

    @include view-s-up() {
      margin-bottom: rem-calc(90);

      figure {
        margin-bottom: rem-calc(90);
      }
    }

    @include view-m-up() {
      margin-bottom: rem-calc(120);

      figure {
        margin-bottom: rem-calc(120);
      }
      h1 {
        margin-bottom: rem-calc(60);
      }
    }
  }
}
