// fix the white borders around the blur
html {
  background-color: var(--theme-color);
}

body {
  margin: 0;
  color: var(--main-text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html[data-darkmode="true"] {
  --main-text-color: white;
  --main-background-color: var(--color-black);
}

figcaption {
  position: absolute;
  // bottom: rem-calc(-50);
  left: 10%;
  bottom: rem-calc(-30);
  height: rem-calc(60);
  display: flex;
  align-items: flex-end;
  span {
    transform: translate(30px, 50%);
    font-size: rem-calc(18);

    @include font-mono();

    // position: absolute;
  }

  &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: rem-calc(20);
    height: 100%;
    border-color: currentColor;
    border-left: 1px solid;
    border-bottom: 1px solid;
  }
  &:after {
    content: "";
    width: rem-calc(10);
    height: rem-calc(10);
    background-color: currentColor;
    position: absolute;
    left: rem-calc(-5);
    top: rem-calc(-5);
    border-radius: 100%;
  }
}

.modal-root {
  position: fixed;
  // top:0;
  // left:0;
  // width:100%;
  // height:100%;
  // z-index: 1000;
  z-index: 100;
}

// ----
.overlay .overlay__close {
  position: relative;

  @include view-m-up() {
    position: absolute;
    display: none;
  }
}

// fix to make layer transition right
// before this, the chapter would not overlay the home overlay
.page-container {
  position: relative;

  // &.is-entering {
  //   z-index: 1000;
  // }
}

.button-icon {
  display: block;
  display: inline-flex;
  align-items: center;
  white-space: nowrap;

  color: var(--main-text-color);
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    text-decoration: underline;
  }

  .icon {
    margin-left: 0.5em;

    width: rem-calc(25);
    height: rem-calc(25);

    svg {
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      position: absolute;
      width: 100%;
      height: 100%;
    }
  }

  @include font-mono();
  font-size: rem-calc(16);
  @include view-s-up() {
    font-size: rem-calc(18);
  }
}
