// -- Index Panel ------------------------------------------------------- //
.index-panel {
  position: relative;
  margin-left: auto;
  width: 100%;
  //   border: 2px solid black;
  // @include view-s-up() {
  //   width: span(8 of 12);
  // }

  // @include view-m-up() {
  //   width: span(6 of 12);
  // }
}

.index-panel__trigger {
  border-bottom: 1px solid currentColor;
  text-align: left;
  z-index: 10;
  position: relative;
  cursor: pointer;

  font-size: rem-calc(16);

  padding-top: rem-calc(7);
  padding-bottom: rem-calc(7);

  @include view-s-up() {
    padding-top: rem-calc(9);
    padding-bottom: rem-calc(9);
  }
}

.index-trigger__current {
  display: flex;

  .index-trigger__chapter-nr {
    width: span(1 of 6, $susy);
  }
  .index-trigger__chapter-title {
    flex: 1;
    padding-right: rem-calc(30);
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  @include view-s-up() {
    .index-trigger__chapter-nr {
      width: rem-calc(30);
    }
    .index-trigger__chapter-title {
      padding-right: rem-calc(100);
    }
  }
}

.index-panel__layer {
  pointer-events: none;
  text-align: left;

  position: absolute;
  background-color: var(--main-background-color);
  height: 100vh;

  display: flex;
  align-items: flex-start;
  opacity: 0;

  overflow: hidden;
  margin-left: 0;
  margin-right: 0;

  width: 100vw;

  padding-top: rem-calc(2);
  transform: translateY(-100%);
  &.is-visible {
    opacity: 1;
    pointer-events: all;
  }

  margin-left: -$margin-xs;

  @include view-s-up() {
    top: -10px;

    margin-left: -gutter($susy-s);
    width: calc(100% + #{$margin-s} + #{gutter($susy-s)});
    margin-right: calc(0% - #{$margin-s});
  }

  @include view-m-up() {
    padding-top: rem-calc(60);

    margin-left: -gutter($susy-m);
    width: calc(100% + #{$margin-m} + #{gutter($susy-m)});
    margin-right: calc(0% - #{$margin-m});
  }
}

.index-panel__list {
  display: block;
  overflow: auto;
  height: 100%;
  width: 100%;

  list-style-type: none;
  overflow: auto;
  padding-top: rem-calc(60);
  padding-bottom: rem-calc(100);

  span,
  h3 {
    margin-top: 0;
  }

  span {
    @include font-body();
    text-transform: uppercase;
  }
  h3 {
    @include font-heading();
  }

  li {
    position: relative;
    width: 90%;
    transform: translateX(0px);

    transition: transform 500ms cubic-bezier(0.34, 1.56, 0.64, 1);

    figure {
      display: block;
      position: absolute;
      width: 44px;
      height: 36px;
      left: 0;
      top: 1.5em;
      opacity: 0;
      transform: translateX(-160%);
      transition: transform 500ms cubic-bezier(0.34, 1.56, 0.64, 1),
        opacity 250ms ease;

      svg {
        path {
          fill: currentColor;
        }
      }
    }
  }
  li:hover,
  li.is-active {
    // margin-left: calc(0px - #{span(1 wide of 9)});

    transform: translateX(60px);

    figure {
      opacity: 1;
      transform: translateX(-150%);

      // transform: translateX(-100%);
    }
  }
  li {
    pointer-events: all;
    margin-bottom: rem-calc(30);
    button {
      cursor: pointer;
    }
  }

  padding-left: $margin-xs;
  padding-right: $margin-xs;

  @include view-s-up() {
    // margin-left: 0 - gutter($susy-m);
    padding-left: rem-calc(60);
    padding-right: $margin-s;
  }
  @include view-m-up() {
    // margin-left: 0 - gutter($susy-m);
    padding-left: rem-calc(70);
    padding-right: $margin-m;
  }

  @include view-m-up() {
    li {
      margin-bottom: rem-calc(40);
    }
  }
}

// -- Header Bar Color ---------------------------------------------------------- //
.header-bar {
  background-color: var(--theme-color);
  --color-background: var(--theme-color);

  &.is-something {
    background-color: var(--main-background-color);
    --color-background: var(--main-background-color);
  }
}

html[data-darkmode="true"] {
  .header-bar {
    background-color: var(--main-background-color);
    --color-background: var(--main-background-color);
  }
}

// -- Index Trigger Progress ------------------------------------------------------ //
.index-trigger__progress {
  width: 100%;
  position: absolute;
  bottom: 0;
  //   background-color: green;
  height: 2px;
}

.index-trigger__toggle {
  display: inline-flex;
  align-items: center;

  position: absolute;
  right: rem-calc(-4);
  top: 50%;
  font-size: rem-calc(12);
  transform: translateY(-50%);
  text-transform: uppercase;
  span {
    display: none;
  }

  .icon {
    width: 30px;
    height: 30px;
    svg {
      width: 100%;
      height: 100%;
    }
  }

  &.is-enabled {
    figure {
      transform: rotate(180deg);
    }
  }

  @include view-s-up() {
    .icon {
      width: 20px;
      height: 20px;
    }
    span {
      display: inline-block;
    }
  }
}

.scroll-progress {
  position: relative;
  width: 100%;
  height: 100%;

  .scroll-progress__bar {
    width: 100%;
    transition: transform 250ms ease;
    transform-origin: left;
    transform: scaleX(0);
    height: 100%;
    // background-color: red;
    background-color: var(--theme-color);
  }
}

