.bullets-hands {
  li.is-active {
    // color: red;
  }
  li {
    position: relative;
    margin-bottom: rem-calc(30);
    width: span(5 of 6);

    figure {
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      width: span(1 wide of 5);
      transform: translateX(-100%);

      svg {
        width: 44px;
        height: 36px;
      }
    }

    // transitions

    figure {
      transition: opacity 250ms ease,
        transform 500ms cubic-bezier(0.34, 1.56, 0.64, 1);
    }
    div {
      transition: transform 500ms cubic-bezier(0.34, 1.56, 0.64, 1);
    }

    &.is-active {
      figure {
        opacity: 1;
        transform: translateX(0);
      }
      div {
        transform: translateX(#{span(1 wide of 5)});
      }
    }
  }

  @include view-s-up() {
    li {
      width: span(10 of 12);
      figure {
        width: span(1 wide of 10);
        // transform: translateX(-100%);
      }

      &.is-active {
        div {
          transform: translateX(#{span(1 wide of 10)});
        }
      }
    }

    // }
  }

  @include view-m-up() {
    li {
      width: span(8 of 12);
      margin-left: span(1 wide of 12);
      margin-bottom: rem-calc(40);

      figure {
        svg {
          position: absolute;
          right: 0;
        }
      }
      &.is-active {
        div {
          transform: translateX(#{span(1 wide of 8)});
        }
      }
    }
    // margin-top: rem-calc(40);
    // margin-left: span(4 wide of 12);
    // width: span(8 of 12);

    // li {
    //   // transition: margin-left 500ms cubic-bezier(0.34, 1.56, 0.64, 1);
    //   // margin-left: calc(0px - #{span(1 wide of 8)});
    //   transform: translateX(calc(0px - #{span(1 wide of 8)}));

    //   margin-bottom: rem-calc(40);
    // }
  }
}

