.chapter {
  width: 100%;
  // box-shadow: -2px 10px 43px 97px rgba(0, 0, 0, 0.75);
  // transform: translateX(100px);
  .bio-link {
    cursor: pointer;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
}

.chapter-page__intro {
  @include view-s-up() {
    p {
      width: span(9 of 12);
      margin-left: span(3 wide of 12);
    }
  }

  @include view-m-up() {
    p {
      width: span(8 of 12);
      margin-left: span(4 wide of 12);
    }
  }
}

.chapter-page__body {
  padding-top: rem-calc(90);
  padding-bottom: rem-calc(60);
  @include view-s-up() {
  }
  @include view-m-up() {
    padding-top: rem-calc(160);
    padding-bottom: rem-calc(160);
  }
}
.chapter[data-chapter="03"] {
  .chapter-page__body {
    font-family: $font-serif;
  }
}

.chapter-page__splash {
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  // justify-content: flex-end;

  // padding: 40px;
  padding-top: rem-calc(150);
  padding-bottom: rem-calc(40);

  // svg {

  // }

  .chapter-page__number {
    display: block;
    width: 60px;
    height: 60px;
    top: 0;
    transform: scale(3);
    color: var(--main-background-color);

    mask-image: url($path-assets + "/mask-enneagon.svg");
    mask-size: 100%;
    background-color: var(--main-text-color);

    display: flex;
    justify-content: center;
    align-items: center;

    .icon-number {
      width: 50%;
      position: relative;
      color: var(--main-background-color);
    }
  }

  .chapter-page__title {
    display: block;
    margin-top: rem-calc(40);
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    svg {
      width: 100%;
      height: auto;
      g {
        // fill: red;
        path {
          fill: currentColor;
        }
      }
      // g polygon {
      //   fill: currentColor;
      // }
    }
  }
  h1 {
    margin-top: 300px;
    font-size: 100px;
    max-width: 50vw;
  }

  @include view-s-up() {
    padding-top: rem-calc(150);

    .chapter-page__number {
      width: 90px;
      height: 90px;
    }
    .chapter-page__title {
      width: 95%;
      margin-top: rem-calc(60);
    }
  }

  @include view-m-up() {
    .chapter-page__title {
      width: span(8 of 12, $susy-m);
    }
  }
}

// chapter title colors
.chapter-page__splash .chapter-page__title {
  color: var(--color-black);
}
html[data-darkmode="true"] {
  .chapter-page__splash .chapter-page__title {
    color: var(--theme-color);
  }
}

//
.chapter-page__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100vh;

  background-color: var(--theme-color);
  transition: background-color 500ms ease;

  &.has-background-reset {
    background-color: var(--main-background-color);
  }

  .chapter-page__next {
    margin-top: auto;
  }
}

html[data-darkmode="true"] {
  .chapter-page__inner {
    background-color: var(--main-background-color);
  }
}

// -- Chapter Page Next ----------------------------- //
.chapter-page__next {
  cursor: pointer;
  position: relative;
  overflow: hidden;
  background-color: var(--theme-color);
  text-align: center;

  padding-top: rem-calc(30);
  padding-bottom: rem-calc(60);

  .subtitle,
  .tagline {
    @include font-body-s();
    display: inline-block;
    @include font-mono();
    text-transform: uppercase;
    margin-bottom: rem-calc(5);
  }

  @include view-s-up() {
    padding-top: rem-calc(20);
  }

  @include view-m-up() {
    padding-top: rem-calc(40);
    padding-bottom: rem-calc(65);
  }
}

// -- Page ------------------------------- //
.page-title {
  // padding-top: rem-calc(60);
  // padding-bottom: rem-calc(60);
  text-align: center;
  .subtitle,
  .tagline {
    @include font-body-s();
    @include font-mono();
    display: inline-block;
    text-decoration: underline;
    margin-bottom: rem-calc(20);
  }
  h1 {
    @include font-heading-xl();

    // font-family: $font-mono;
  }

  .subtitle {
    text-transform: uppercase;
    text-decoration: none;
  }

  @include view-s-up() {
    margin-bottom: rem-calc(90);
  }
  @include view-m-up() {
    margin-bottom: rem-calc(120);

    .subtitle,
    .tagline {
      margin-bottom: rem-calc(40);
    }
  }
}

.chapter[data-chapter="03"] {
  .chapter-page__body header + * {
    // article line
    &:before {
      content: "";
      width: 100px;
      height: 1px;
      background-color: currentColor;
      display: block;
      margin-left: auto;
      margin-right: auto;
      transform: translateY(-10px);

      @include view-s-up() {
        width: 130px;
      }
      @include view-m-up() {
        width: 180px;
      }
    }
  }
  .page-title {
    // color: red;
    h1 {
      @include font-heading-tiempos();
    }

    .subtitle,
    .tagline {
      font-size: rem-calc(16);
    }
    .subtitle {
      margin-top: rem-calc(30);
      font-size: rem-calc(14);
      width: 50%;
      margin-bottom: 0;
    }

    @include view-s-up() {
      .subtitle {
        width: span(5 of 12);
      }
    }
    @include view-m-up() {
      .subtitle,
      .tagline {
        font-size: rem-calc(18);
      }
      h1 {
        width: span(9 wider of 12);
        margin-left: auto;
        margin-right: auto;
      }
      .subtitle {
        margin-top: rem-calc(60);
      }
    }
  }
}

.chapter[data-chapter="05"] {
  .page-title {
    .subtitle,
    .tagline {
    }

    .tagline {
      @include font-body-s();
      font-family: $font;
      font-size: rem-calc(14);
      text-decoration: none;
      margin-bottom: rem-calc(16);
    }

    .tagline__author {
      text-decoration: underline;
    }
    @include view-m-up() {
      .subtitle,
      .tagline {
        margin-bottom: rem-calc(20);
        font-size: rem-calc(16);
      }
    }
  }
}

.chapter[data-chapter="06"] {
  .page-title {
    span {
      margin-bottom: rem-calc(10);
    }
    span.subtitle {
      display: none;
    }
  }

  @include view-m-up() {
    .page-title {
      span.subtitle {
        text-transform: uppercase;
        button {
          text-transform: uppercase;
        }
        font-size: rem-calc(16);
        margin-top: rem-calc(60);
        display: block;
      }
    }
  }
}
