.mozfest-footer {
  //   background-color: var(--color-black);
  color: var(--main-background-color);
  background-color: var(--main-text-color);
  padding-top: rem-calc(40);
  padding-bottom: rem-calc(40);

  font-size: rem-calc(18);
  font-family: $font-nunito;
  h5 {
    font-size: rem-calc(20);
    font-family: $font-zilla;
    margin-bottom: 1rem;
  }
  p {
    margin-bottom: 1em;
  }
  p a {
    color: var(--color-purple);
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }

  @include view-s-up() {
    h5 {
      font-size: rem-calc(22);
    }
  }
}

.mozfest-footer__column:first-child {
  .mozilla-logo {
    display: block;
    margin-top: 3rem;
    margin-bottom: 3rem;
    width: rem-calc(96);
    color: currentColor;

    svg {
      path {
        fill: currentColor;
      }
    }
  }
  @include view-s-up() {
    .mozilla-logo {
      margin-bottom: 0;
    }
  }
}
.mozfest-footer__inner {
  display: flex;
  flex-direction: column;

  @include view-s-up() {
    flex-direction: row;
    justify-content: space-between;
    > div {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      width: span(6 of 12, $susy-s);
      //   justify-content: space-between;
    }
  }

  @include view-m-up() {
    > div {
      width: span(5 of 12, $susy-m);
      //   justify-content: space-between;
    }
  }
}

.mozfest-footer__foundation {
  border-top: 1px solid currentColor;
  font-size: rem-calc(12);
  padding-top: 1rem;
  margin-top: 2rem;
  p {
    margin-bottom: 0;
  }
}

.mozfest-footer__more-nav {
  @include view-m-up() {
    display: flex;

    .link-list {
      column-count: 2;
    }

    .mozfest-footer__more {
      flex-shrink: 0;
      width: span(2 of 5, $susy-m);
    }
    .mozfest-footer__nav {
      margin-left: gutter($susy-m);
    }
  }
}

.mozfest-footer__more {
  margin-bottom: 2rem;
  ul {
    display: flex;
    li {
      margin-right: 0.5rem;
      margin-bottom: 0.25rem;
      a {
        color: currentColor;
      }
      svg {
        width: 80%;
        path {
          fill: currentColor;
        }
      }
    }
  }
}

.mozfest-footer__nav {
  font-size: rem-calc(18);

  a {
    color: currentColor;
    text-decoration: none;

    &:hover {
      color: #595cf3;
      text-decoration: underline;
    }
  }

  li {
    margin-bottom: 0.5rem;
  }
}

// btn
.mozfest-footer .btn {
  display: block;
  color: #fff;
  background-color: #595cf3;
  border-color: transparent;
  padding: 0.5rem 1rem;
  font-family: Nunito Sans;
  font-weight: 700;
  white-space: nowrap;
  font-size: 18px;
  line-height: 1.2222222222;
  text-decoration: none;
  text-align: center;

  @include view-s-up() {
    display: inline-block;
    text-align: left;
  }
}

