figure {
  position: relative;
  img {
    width: 100%;
    // position: absolute;
    // width:100%;
    // height:100%;
  }
}

figure.is-aspect {
  position: relative;
  display: block;
  @include aspect-ratio();

  &:after {
    padding-top: var(--aspect-ratio);
  }
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    left:0;
    top:0;
    object-fit: cover;
  }
}
