.app-header {
  position: fixed;
  z-index: 100;
  width: 100%;

  // ul > li .active {
  //   color: red;
  // }
}

// // -- Nav positioning ---
.app-header > .book-chapters-nav {
  width: 100%;
  position: absolute;
  top: 50vh;
  transform: translateY(-50%);
  z-index: 1000;
  display: none;
  @include view-m-up() {
    display: block;
  }
}

// .overlay-menu {
